.latest-stations-article {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 85px 1fr 95px;
    align-items: center;
    width: 100%;
    background-color: #2d3030;
    border-radius: 4px;
    &:hover {
        background-color: #464a4a;
        .play-button {
           opacity: 1;
        }
    }
    span {
        padding: rem(15px);
        font-size: rem(16px);
        font-family:  circular-medium;
        min-width: rem(200px);
        max-width: rem(200px);
    }
    img {
        
        width: rem(85px);
        border-top-left-radius: rem(4px);
        border-bottom-left-radius: rem(4px);
    }
    .play-button {
       opacity: 0;
       margin-left: rem(25px);
    }
}